<template>
     <div class="inputBox shawdow">
        <!-- v-on:keyup.enter : 엔터키 클릭시 동작하는 이벤트-->
        <input type="text" v-model="newTodoItem" placeholder="금일 할일을 작성하세요" v-on:keyup.enter="addTodo">
        &nbsp;&nbsp;
        <span class="addContainer" v-on:click="addTodo">
            <i class="addBtn fa fa-plus" aria-hidden="true"></i>   
        </span>
     </div> 
</template>

<script>
export default {
    //인스턴스 생성
    data() {
        //데이터 입력 및 데이터 바인딩
        return{
            newTodoItem: ''
        }
    },
    //함수 호출하는 부분
    methods : {
        addTodo(){
            //입력된 값이 있을경우에 저장되게
            if(this.newTodoItem !== ""){
                var val = this.newTodoItem && this.newTodoItem.trim();
                //로컬 스토리지에 넣어보기 -> local stroage에 저장됨
                //이부분 추후에 firebase로 변경하여 수정할 예정 - 박응서
                localStorage.setItem(val, val);
                this.$emit('addTodo', val);
                this.clearInput();
            }
        },
        clearInput() {
            this.newTodoItem = '';
        }
    }
}
</script>

<style scoped>
    input:focus{
        outline: none;
    }
    .inputBox{
        background: white;
        height: 50px;
        line-height: 50px;
        border-radius: 5px;
    }
    .inputBox input{
        border-style: none;
        font-size: 0.9rem;
    }
    .addContainer{
        float: right;
        background: linear-gradient(to right, #6478fb, #8763fb);
        display: inline-block;
        width: 3rem;
        border-radius: 0 5px 5px 0;
    }
    .addBtn {
        color: white;
        vertical-align: middle;
    }
</style>
  